<template>
    <div>
        <form>
            <div v-if="!ifPlayerNameChange">
                <span>輸入玩家名稱</span>
                <input :value="playerName" @change="changePlayerName" >
            </div>
            <button @click="showInput" v-else>更改名稱</button>
            <!-- <input type="text" placeholder="輸入玩家名稱" :value="this.$store.state.playerName"> -->
            <!-- <input type="submit" value="決定" @submit.prevent="updatePlayerName"> -->
        </form>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: 'EnterPlayerName',
    data() {
        return {
            ifPlayerNameChange: false
        }
    },
    computed: {
        ...mapState({
            playerName: state => state.playerName
        })
    },
    methods: {
        changePlayerName (e) {
            e.preventDefault();
            this.$store.commit('changePlayerName', e.target.value);
            this.ifPlayerNameChange = true;
        },
        showInput () {
            this.ifPlayerNameChange = false;
        }
    }
}
</script>

<style scoped>
input {
    box-sizing: border-box;
    width: 100%;
    padding: 10px; margin-top: 10px;
}
@media (min-width:600px) {
    input {
        margin-left: 10px;
        width: 50%;
    }
}
</style>