<template>
    <div class="game-scene">

        <!-- 補牌 -->
        <div class="refill-container" v-if="ifBotLose">
            <h2>恭喜你粉碎一隻 GKBOT！</h2>
            <div class="bot-chat-area special">
                <p>可惡的人類，居然用外表令我低估你的實力...我認輸了，你就從我剩餘的牌堆中補充牌吧！補完了就趕快離開，我要自爆了！</p>
            </div>
            <div class="system-msg">
                <p>{{ refillMsg }}</p>
            </div>
            <div class="card-set">
                <li v-for="(card, index) in botCard" :key="index" @click="refillCard(index)">{{card}}</li>
            </div>
            <hr>
            <div class="card-set">
                <li v-for="(card, index) in playerCard" :key="index" @click="throwCardAway(index)">{{card}}</li>
            </div>
            <button @click="nextEnemy">補充完畢，迎戰下一隻 GKBOT!</button>
        </div>

        <!-- 遊戲畫面 -->
        <div class="game-scene" v-else>

            <!-- 機器人閒聊 -->
            <div class="bot-chat-area">
                <p>{{botMsg}}</p>
            </div>

            <!-- 機器人牌組 -->
            <div class="bot-card-area card-set">
                <li class="bot-card-small" v-for="(card, index) in botCard" :key="index"></li>
            </div>

            <!-- 牌桌 -->
            <div class="desk card-set">
                <div class="bot-desk">
                    <li class="flip-card" v-for="(card, index) in botDesk" :key="index">
                        <div class="flip-card-inner" :class="{'show': showCard}">
                            <div class="flip-card-front"></div>
                            <div class="flip-card-back">{{card}}</div>
                        </div>
                    </li>
                </div>
                <div class="player-desk card-set">
                    <li v-for="(card, index) in playerDesk" :key="index" @click="throwCardBack">{{card}}</li>
                </div>
            </div>

            <!-- 系統訊息 -->
            <div class="system-msg">
                <span>第 {{round}} 把，{{playerWinNum}} 勝 {{botWinNum}} 負，</span>
                <span v-if="rollResult===1">這把比大</span>
                <span v-else-if="rollResult===2">這把比小</span>
                <span v-else>抽籤來決定這把比大或小</span>
                <p>{{systemMsg}}</p>
            </div>

            <!-- 按鈕群組 -->
            <div class="btn-group">
                <button class="roll-btn" v-if="!rolled" @click="rollDice">抽籤</button>
                <button class="duel-btn" v-else @click="duel">開牌</button>
            </div>

            <!-- 玩家牌組 -->
            <div class="player-card-area card-set">
                <li v-for="(card, index) in playerCard" :key="index" @click="throwCard(index)">{{card}}</li>
            </div>

        </div>
        
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: 'Game',
    data() {
        return {
            playerCard: [1,2,3,4,5,6,7],
            botCard: [1,2,3,4,5,6,7],
            ifBotLose: false,
            rolled: false,
            thisRoll: 0,
            rolling: false,
            rollResult: 0,
            showCard: false,
            playerCardChoosed: '',
            botCardChoosed: '',
            playerDesk: [],
            botDesk: [],
            round: 1,
            playerWinNum: 0,
            botWinNum: 0,
            refillMsg: '',
            systemMsg: '',
            botMsg: ''
        }
    },
    computed: {
        ...mapState({
            ifGameOver: state => state.ifGameOver
        })
    },
    methods: {
        systemSay(msg) {
            this.systemMsg = msg;
            let v = this;
            setTimeout(function(){ v.systemMsg = ''; } ,2000);
        },
        throwCard(index) {
            this.botChat('throw-card');
            if (this.playerDesk.length === 0){
                this.playerCardChoosed = this.playerCard[index];
                this.playerDesk.push(parseInt(this.playerCardChoosed));
                this.playerCard.splice(index,1);
            } else {
                this.systemSay('牌桌滿了，別再按了！');
            }
        },
        throwCardBack() {
            if (this.playerDesk.length === 0){
                return;
            } else {
                this.playerCard.push(parseInt(this.playerCardChoosed));
                this.smallToBig();
                this.playerDesk = [];
            }
        },
        rollDice() {
            if (this.thisRoll === 0) { // 2秒後顯示抽籤結果
                this.systemSay('抽籤中...');
                this.rolling = true;
                this.thisRoll = Math.floor(Math.random()*2)+1; //擲骰子1~2
                let v = this;
                setTimeout(function(){ v.rolling=false },2000);
                setTimeout(function(){ v.showRollResult() }, 2000);
            } else {
                return;
            }
        },
        showRollResult() {
            this.rolled = true; //隱藏抽籤按鈕，顯示決鬥按鈕
            if (this.thisRoll === 1) { //比大
                this.rollResult = 1;
            } else if (this.thisRoll === 2) { //比小
                this.rollResult = 2;
            }
            // 2秒後機器人出牌
            let v = this;
            setTimeout(function(){ v.botLogic() }, 2000);
        },
        botLogic() {
            if (this.rollResult === 1) {
                // 隨機取出一個數，如果比一半的數字小，就重抽
                let index = Math.abs(Math.floor(Math.random()*this.botCard.length-1));
                let midIndex = Math.floor(this.botCard.length/2)-1;
                if (this.botCard.length < 3) {
                    alert("GKBOT牌不夠，不玩了！QQ");
                } else {
                    if (this.botCard[index] > this.botCard[midIndex]) {
                        this.botThrowCard(index);
                    } else {
                        this.botLogic();
                    }
                }
            } else if (this.rollResult === 2) {
                let index = Math.abs(Math.floor(Math.random()*this.botCard.length-1));
                let midIndex = Math.floor(this.botCard.length/2)+1;
                 if (this.botCard.length < 3) {
                    alert("GKBOT牌不夠，不玩了！QQ");
                } else {
                    if (this.botCard[index] < this.botCard[midIndex]) {
                        this.botThrowCard(index);
                    } else {
                        this.botLogic();
                    }
                }
            }
        },
        botThrowCard(index) {
            this.botCardChoosed = this.botCard[index];
            this.botDesk.push(parseInt(this.botCardChoosed));
            this.botCard.splice(index,1);
            this.botChat('bot-throw-card');
        },
        duel(){
            let p = parseInt(this.playerDesk[0]);
            let b = parseInt(this.botDesk[0]);
            if (this.botDesk.length === 0 || this.playerDesk.length === 0){
                this.systemSay('還有人沒出牌呢！');
                return;
            } else {
                this.showCard = true;
                if (this.rollResult === 1) { //比大
                    if ( p > b ) { //玩家贏
                        this.playerWin();
                    } else if ( p < b ) { //機器人贏
                        this.botWin();
                    } else { //平手
                        this.draw();
                    }
                } else if (this.rollResult === 2) { //比小
                    if ( p < b ) { //玩家贏
                        this.playerWin();
                    } else if ( p > b ) { //機器人贏
                        this.botWin();
                    } else { //平手
                        this.draw();
                    }
                }
            }
        },
        draw() {
            this.systemSay('平手！正在抽籤定勝負...');
            let v = this;
            setTimeout(function(){
                let a = Math.floor(Math.random()*2)+1;
                if (a===1) { //玩家贏
                    v.playerWin();
                } else { //機器人贏
                    v.botWin();
                }
            } ,4000);
        },
        playerWin() {
            this.botChat('player-win');
            let v = this;
            setTimeout(function(){v.systemSay('玩家贏！')} ,1000);
            setTimeout(function(){v.playerWinNum++} ,2000);
            setTimeout(function(){v.clearDesk()}, 2000);
        },
        botWin() {
            this.botChat('bot-win');
            let v = this;
            setTimeout(function(){v.systemSay('機器人贏！')} ,1000);
            setTimeout(function(){v.botWinNum++} ,2000);
            setTimeout(function(){v.clearDesk()}, 2000);
        },
        nextEnemy() {
            let v = this;
            setTimeout(function(){ 
                v.botCard = [1,2,3,4,5,6,7];
                v.ifBotLose = false;
                v.playerDesk = [];
                v.botDesk = [];
                v.rolled = false;
                v.thisRoll = 0;
                v.rollResult = 0;
                v.round = 1;
                v.playerWinNum = 0;
                v.botWinNum = 0;
                v.showCard = false;
            } ,2000);
        },
        clearDesk() {
            if (this.playerWinNum === 2) { //進入補牌
                this.systemSay('2勝，你打敗一隻機器人了！');
                this.updateRecord();
                let v = this;
                setTimeout(function(){ v.ifBotLose = true; } ,3000);
            } else if (this.botWinNum === 2) { //玩家滅亡
                this.systemSay('2敗，你輸了！');
                let v = this;
                setTimeout(function(){ v.$store.commit('changeGameState', 2); } ,3000);
            } else if (this.playerCard.length === 0) { //玩家滅亡
                this.systemSay('沒牌了！你被GKBOT的激光殺死了！');
                let v = this;
                setTimeout(function(){ v.$store.commit('changeGameState', 2); } ,3000);
            } else { //清空桌子&重設抽籤
                this.playerDesk = [];
                this.botDesk = [];
                this.rolled = false;
                this.thisRoll = 0;
                this.rollResult = 0;
                this.round++;
                this.showCard = false;
            }
        },
        updateRecord() {
            this.$store.commit('updateRecord');
        },
        refillCard(index) {
            if (this.playerCard.length === 7) {
                this.refillMsg = '不好意思，GK說牌不能多於七張';
                return;
            } else {
                this.refillMsg = '';
                this.playerCardChoosed = this.botCard[index];
                this.botCard.splice(index,1);
                this.playerCard.push(parseInt(this.playerCardChoosed));
                this.smallToBig();
            }
        },
        throwCardAway(index) {
            if (this.playerCard.length === 3) {
                this.refillMsg = '不好意思，GK說牌不能少於三張';
                return;
            } else {
                this.refillMsg = '';
                this.playerCardChoosed = this.playerCard[index];
                this.playerCard.splice(index,1);
                this.botCard.push(parseInt(this.playerCardChoosed));
                this.smallToBig();
            }
        },
        smallToBig() {
            this.playerCard.sort((a, b) =>  a - b );
            this.botCard.sort((a, b) =>  a - b );
        },
        botChat(status) {
            let ranNum = Math.floor(Math.random()*3)+1; //1~3
            let v = this;
            if (status === 'throw-card') { //玩家出牌時說話
                if (ranNum === 1){
                    setTimeout(function(){ v.botMsg='Seriously？你真的打算出那張牌嗎？' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 2) {
                    setTimeout(function(){ v.botMsg='想這麼久就出這張啊？笑死我了' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 3) {
                    setTimeout(function(){ v.botMsg='zzZ' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                }
            } else if (status === 'bot-throw-card') { //機器人出牌時說話
                if (ranNum === 1){
                    setTimeout(function(){ v.botMsg='hmm...這張好了，不不不，還是這張吧' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 2) {
                    setTimeout(function(){ v.botMsg='誰說比大的時候我只會出比較大的？' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 3) {
                    setTimeout(function(){ v.botMsg='就決定是這張了！我可是有放水喔' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                }
            } else if (status === 'player-win') { //玩家贏時說話
                if (ranNum === 1){
                    setTimeout(function(){ v.botMsg='欸！你是不是出老千啊' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 2) {
                    setTimeout(function(){ v.botMsg='逼波逼波逼波...GK 博士說你出千' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 3) {
                    setTimeout(function(){ v.botMsg='我想補充一點機油了...' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                }
            } else if (status === 'bot-win') { //機器人贏時說話
                if (ranNum === 1){
                    setTimeout(function(){ v.botMsg='TOLD YOU!!!!' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 2) {
                    setTimeout(function(){ v.botMsg='嗄嗄嗄！人類好弱！太弱啦！' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                } else if (ranNum === 3) {
                    setTimeout(function(){ v.botMsg='你接下來會說「這怎麼可能？」說吧！' } ,1000);
                    setTimeout(function(){ v.botMsg='' } ,3000);
                }
            }
        }
    }
}
</script>

