import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    ifGameOver: 0,
    gkBotMarathonHistory: [],
    playerName: '運氣不好的路人',
    record: 0
  },
  mutations: {
    changeGameState (state, controller) {
      state.ifGameOver = controller;
    },
    changePlayerName (state, playerName) {
      state.playerName = playerName;
    },
    getHistory (state) {
      state.gkBotMarathonHistory = JSON.parse(localStorage.getItem('gkBotMarathonHistory'));
    },
    updateRecord (state) {
      let oldData = JSON.parse(localStorage.getItem('gkBotMarathonHistory'));
      let data = {
        playerName: state.playerName,
        record: 0
      };
      if (oldData===null || oldData===[]) { //若storageData是空的
          data.record++;
          oldData = [];
          oldData.push(data);
      } else { //storageData已有資料
          for (let i =0; i<oldData.length; i++) {
            if (state.playerName === oldData[i].playerName) { //找到相同名字玩家
              oldData[i].record++;
            } else { //新的玩家
              data.record++;
              oldData.push(data);
              break;
            }
          }
      }
      localStorage.setItem('gkBotMarathonHistory' ,JSON.stringify(oldData));
    },
    eraseStorage() {
      localStorage.removeItem('gkBotMarathonHistory');
    }
  }
})
