<template>
  <div id="app">

    <!-- 導覽列 -->
    <div class="nav">
      <span>{{playerName}}，您好！</span>
      <div class="nav-btn-group">
        <button @click="backToMenu">重新開始</button>
        <button @click="showRecords">戰績</button>
        <button @click="showRules = !showRules">規則</button>
      </div>
    </div>

    <div class="menu">
      <!-- 規則 -->
      <div class="rules" v-show="showRules">
          <h2>遊戲規則</h2>
          <li>源源不絕的 GKBOT 將試圖消滅你</li>
          <li>每場三把決勝負，三戰兩勝</li>
          <li>每把需抽籤決定比大／比小，再出牌</li>
          <li>若平手則由系統抽籤決定勝負</li>
          <li>輸一場就會被 GKBOT 消滅</li>
          <li>為了捍衛自己的生命，請你加油吧！</li>
          <button @click="showRules = !showRules">關閉</button>
      </div>

      <!-- 戰績 -->
        <div class="records" v-show="showingRecords">
          <h2>歷史戰績</h2>
          <li v-for="(player, index) in gkBotMarathonHistory" :key="index">{{player.playerName}}：粉碎 {{player.record}} 隻GKBOT</li>
          <div class="btn-group">
            <button @click="eraseStorage">清除紀錄</button>
            <button @click="showRecords">關閉</button>
          </div>
        </div>

      <!-- 主畫面 -->
      <div v-if="ifGameOver===0">

        <h2>《 決戰 GKBOT 馬拉松 》</h2>
        <p class="content">邪惡的 GK 博士創造出了具有自我意識、且會不斷自我複製的機器人：GKBOT。
        <br>
        <br>GKBOT 判定人類對地球有害，因此會與他們遇到的每一個人類展開賭上性命的對決。奇特的是，他們的對決方式是撲克牌比大小。
        <br>
        <br>
        快看！前方就有一隻 GKBOT 正朝你靠近，那麼，報上名來吧。</p>
        <EnterPlayerName />
        <div>
          <button class="start-btn" @click="startGame">開始對決</button>
          <div class="btn-group">
            <button class="records-btn" @click="showRecords">歷史戰績</button>
            <button class="rules-btn" @click="showRules = !showRules">遊戲規則</button>
          </div>
        </div>
      </div>

      <!-- 遊戲畫面 -->
      <div class="game" v-else-if="ifGameOver===1">
        <Game />
      </div>

      <!-- 遊戲結束畫面 -->
      <div class="game-over" v-else>
        <GameOver />
      </div>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex';
import EnterPlayerName from '@/components/EnterPlayerName.vue';
import Game from '@/components/Game.vue';
import GameOver from '@/components/GameOver.vue';

export default {
  data() {
    return {
      showRules: false,
      showingRecords: false
    }
  },
  components:{
    EnterPlayerName,
    Game,
    GameOver
  },
  methods: {
    startGame() {
      this.$store.commit('changeGameState', 1);
    },
    backToMenu() {
      this.$store.commit('changeGameState', 0);
    },
    showRecords() {
      this.showingRecords = !this.showingRecords;
      this.$store.commit('getHistory');
    },
    eraseStorage() {
      this.$store.commit('eraseStorage');
    }
  },
  computed: {
      ...mapState({
          ifGameOver: state => state.ifGameOver,
          playerName: state => state.playerName,
          record: state => state.record,
          gkBotMarathonHistory: state => state.gkBotMarathonHistory
      })
  }
}
</script>

